'use strict';
import React from 'react';
import ReactDOM from 'react-dom';
import { default as __links } from 'sources/links';

require('styles/pages/contact.less');

const ContactLink = React.createClass({
  render() {
    return (
      <li className="link-item list-item-icon">
        <div className="list-item-icon__icon">
          <a href={ this.props.url } target="_blank"><i className={`icon ${this.props.icon}`}></i></a>
        </div>
      </li>
    );
  }
});


const ContactLinks = React.createClass({
  render() {
    return (
      <ul className="link-items list-icons animated fadeIn">
        { this.props.children }
      </ul>
    );
  }
});


export default React.createClass({
  getDefaultProps() {
    return {
      links: __links,
      style: {
        backgroundImage: `url(${ require('images/site/banana-plants.png') })`
      }
    };
  },
  render() {
    return (
      <div style={ this.props.style } className="page-wrapper" id="contact">
        <ContactLinks>
          <ContactLink key={ this.props.name } url={ this.props.url } icon={ this.props.icon } />
        </ContactLinks>
      </div>
    );
  }
});


